@charset "UTF-8";
#wpcontent {
  padding-left: 0 !important;
}

.content-container {
  padding: 0 20px;
  flex-basis: 100%;
  max-width: 1300px;
  margin: 0 auto;
}

#wpbody-content > .error, #wpbody-content > .info, #wpbody-content > .notice {
  display: none !important;
}

.ob-overflow-off {
  overflow: hidden !important;
}

@media screen and (min-width: 660px) {
  .ob-import-modal {
    width: 630px !important;
  }
}
@media screen and (min-width: 992px) {
  .ob-import-modal {
    width: 700px !important;
  }
}
.container {
  padding: 0 15px;
}
.container.content {
  padding: 0;
}

svg.is-loading, button.is-loading svg {
  animation-name: spin;
  animation-duration: 2000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  margin-right: 0;
}

.col {
  width: 100%;
}

@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.columns.starter-sites {
  position: relative;
  display: flex;
  flex-grow: 1;
}

.ss-background {
  opacity: 0.25;
  z-index: -1;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  display: block;
  background-size: cover;
  background-position: center center;
  left: 0;
  right: 0;
  height: calc(100vh - 300px);
  flex-grow: 1;
}

.unavailable-starter-sites {
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 50px 30px;
  text-align: center;
  align-items: center;
  justify-content: center;
}
.unavailable-starter-sites h1 {
  line-height: 1.6;
}

/*==============*/
.content-wrap.is-onboarding {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f1f1f1;
  z-index: 100000;
  overflow: auto;
}
.content-wrap.is-onboarding h2 {
  display: block;
}
.content-wrap.is-onboarding h2 img {
  display: none;
}
.content-wrap.is-onboarding .content {
  background-color: #fefefe;
  padding-top: 30px;
}
.content-wrap.is-onboarding .ob {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}
.content-wrap.is-onboarding .ob-migration {
  padding-top: 0;
  text-align: center;
}
.content-wrap.is-onboarding .ob-migration h2 {
  line-height: 1;
}
.content-wrap.is-onboarding .ob-head button {
  margin: 0 !important;
}
.content-wrap.is-onboarding .ob-head {
  padding: 20px 0 0;
  margin: 0 0 20px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.content-wrap.is-onboarding .ob-head button.close-onboarding {
  color: #676767;
}
.content-wrap.is-onboarding .ob-head button.close-onboarding svg {
  width: 30px;
  height: 30px;
}
.content-wrap.is-onboarding .content-container {
  padding: 0;
}

.ob {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ob .ob-head {
  box-sizing: border-box;
  padding: 20px;
  background: #ffffff;
  width: 100%;
  display: flex;
  justify-content: center;
}
.ob .ob-head .header-container {
  display: flex;
  flex-basis: 100%;
  max-width: 1300px;
}
.ob .ob-head .header-container h2 {
  flex-basis: 40%;
}
.ob .ob-head .header-container .header-nav {
  flex-basis: 60%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.ob .ob-head .header-container .header-nav .components-button {
  font-size: 18px;
  font-weight: 600;
  color: #282828;
  margin-left: 25px;
}
.ob .ob-head .header-container .header-nav .components-button:hover {
  box-shadow: none;
}
.ob .ob-head .header-container .header-nav .components-button:first-child {
  margin-left: 0;
}
.ob .ob-head .header-container .header-nav .components-button.is-pressed {
  color: #0073AA;
  background: transparent;
}
.ob .ob-body {
  width: 100%;
  box-sizing: border-box;
}
.ob .ob-body .instructions {
  font-size: 22px;
  margin: 20px 0;
  color: #282828;
}

.ob-logo {
  width: 50px;
  margin-right: 20px;
}

.ob-sites {
  margin-top: 30px;
}
.ob-sites.is-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}

.no-results {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
}
.no-results p {
  font-size: 16px;
}
.no-results p span {
  color: #0073AA;
  font-weight: 600;
}
.no-results .tags {
  margin-top: 10px;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
}
.no-results .tags .tag {
  margin: 10px 20px;
  line-height: normal;
  height: auto;
  font-weight: 600;
  padding: 10px 15px;
  color: #fff;
  transition: all 0.3s ease;
}
.no-results .tags .tag:hover {
  background-color: #0071a7;
}

.pro-badge {
  background-color: #6EB77A;
  height: auto;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
  padding: 5px 10px;
  margin-left: auto;
  border-radius: 999px;
  line-height: normal;
  display: flex;
  align-items: center;
}
.pro-badge svg {
  margin-right: 3px;
}

.ob-migration {
  padding-top: 20px;
  margin-bottom: 20px;
}
.ob-migration .card.starter-site-card {
  margin-right: auto !important;
  margin-left: auto !important;
}
.ob-migration .card.starter-site-card .image {
  height: auto;
}
.ob-migration .actions {
  margin: 30px auto 0;
  display: flex;
  justify-content: center;
}
.ob-migration button:last-child {
  margin-left: 10px;
}

.ob-head > .header-container > h2, .ob-migration > h2 {
  font-size: 21px;
  color: #282828;
  margin: 0;
  line-height: 1.6;
  display: flex;
  align-items: center;
}
.ob-head > .header-container > h2 > img, .ob-migration > h2 > img {
  margin-right: 20px;
}
.ob-head > .header-container > p, .ob-migration > p {
  margin: 20px 0;
  font-size: 15px;
  color: #282828;
}
.ob-head > .header-container > button, .ob-migration > button {
  margin-bottom: 20px;
}

.header-form {
  margin-top: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.header-form input {
  margin: 0;
  display: block;
  flex-grow: 1;
  border: 1px solid #e1e1e1;
  height: 52px;
  padding: 5px 30px 5px 50px;
  font-size: 17px;
  font-weight: 300;
}

.is-sync {
  background-color: #f1f1f1;
  color: #0073aa !important;
  border-radius: 6px;
}
.is-sync:before {
  padding: 0 5px 0 15px;
  content: attr(data-content);
  margin-right: 5px;
  font-size: 15px;
  display: none;
}
.is-sync:hover:before {
  display: block;
}

.search {
  position: relative;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}
.search input {
  width: auto;
  flex-shrink: 1;
}
.search img {
  opacity: 0.4;
  position: absolute;
  top: 10px;
  left: 10px;
}
.search .ob-dropdown {
  width: 100%;
  height: 52px;
  margin: 20px 0 0;
}
.search .ob-dropdown button {
  position: relative;
  left: -1px;
  height: 100%;
  display: flex;
  align-items: center;
  font-weight: 600;
  padding: 5px 20px 5px 10px;
  background: #fff;
  border: 1px solid #e1e1e1;
  font-size: 14px;
  margin: 0;
}
.search .ob-dropdown button > svg, .search .ob-dropdown button .dashicon {
  margin-left: auto;
  display: flex;
  align-items: center;
}
.search .ob-dropdown .options {
  margin: 0;
}
.search .ob-dropdown .options li {
  margin: 0;
}
.search .ob-dropdown .options a {
  height: 35px;
  font-size: 14px;
  display: flex;
  align-items: center;
  font-weight: 600;
  color: #282828;
  cursor: pointer;
  padding: 5px 10px;
  text-decoration: none;
  white-space: nowrap;
}
.search .ob-dropdown .options a .count {
  color: #a3a3a3;
}
.search .ob-dropdown .options a > span:not(.count) {
  margin-right: 20px;
}
.search .ob-dropdown .options a:hover {
  color: #fff;
  background-color: #0073AA;
}
.search .ob-dropdown .options a:hover .count {
  color: #fff;
}
.search .ob-dropdown .options a .count {
  margin-left: auto;
}

.editor-tabs {
  display: none;
  margin: 30px 0;
  position: relative;
}
.editor-tabs:after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  left: 0;
  border-bottom: 1px solid #e0e0e0;
  height: 1px;
  z-index: -1;
  right: 0;
  bottom: 0;
}
.editor-tabs .long-name {
  display: none;
}
.editor-tabs a {
  padding: 15px 10px 12px 0px;
  display: flex;
  align-items: center;
  font-weight: 700;
  text-decoration: none;
  font-size: 15px;
  border-bottom: 3px solid transparent;
  position: relative;
  width: auto;
}
.editor-tabs a:last-child {
  padding-right: 0;
}
.editor-tabs a:not(:first-child) {
  margin-left: auto;
}
.editor-tabs a.active {
  border-bottom: 3px solid #0073AA;
}
.editor-tabs a .pro-badge {
  margin-left: auto;
  font-size: 11px;
}
.editor-tabs .editor {
  color: #616161;
}
.editor-tabs .count {
  color: rgba(97, 97, 97, 0.5);
  margin: 0 10px;
}
.editor-tabs .icon-wrap {
  display: none;
  margin-right: 10px;
  width: 34px;
  height: 34px;
  border-radius: 100%;
  overflow: hidden;
}
.editor-tabs .icon-wrap img {
  max-width: 100%;
  display: block;
}

.cloud-items .editor-tabs a:not(:first-child) {
  margin-left: 20px;
}

@media (min-width: 1600px) {
  .editor-tabs .long-name {
    display: inline;
  }
}
.ob-dropdown.editor-selector {
  overflow: visible;
  display: inline-block;
  position: relative;
  width: 100%;
}
.ob-dropdown.editor-selector button {
  display: flex;
  align-items: center;
  font-weight: 600;
  padding: 5px 20px 5px 10px;
  background: #fff;
  border: 1px solid #e1e1e1;
  font-size: 14px;
  height: 52px;
  width: 100%;
  white-space: nowrap;
}
.ob-dropdown.editor-selector button .count {
  color: #a3a3a3;
  margin: 0 20px 0 auto;
}
.ob-dropdown.editor-selector img {
  border-radius: 100%;
  margin-right: 5px;
  max-width: 30px;
  max-height: 30px;
}
.ob-dropdown.editor-selector .options {
  margin: 0;
}
.ob-dropdown.editor-selector .options li {
  border-bottom: 1px solid #e1e1e1;
  padding: 0;
  margin: 0;
}
.ob-dropdown.editor-selector .options a {
  height: 35px;
  font-size: 14px;
  display: flex;
  align-items: center;
  font-weight: 600;
  color: #282828;
  cursor: pointer;
  padding: 5px 10px;
  text-decoration: none;
  white-space: nowrap;
}
.ob-dropdown.editor-selector .options a > span:not(.count) {
  margin-right: 20px;
}
.ob-dropdown.editor-selector .options a .count {
  margin-left: auto;
}
.ob-dropdown.editor-selector .options a:hover {
  background-color: #0073AA;
  color: #fff;
}

.card {
  width: 100%;
  display: flex;
  max-width: unset;
  min-width: unset;
  border: 1px solid #e1e1e1;
  flex-direction: column;
  padding: 30px 40px;
  margin: 0;
  align-self: flex-start;
  flex-grow: 0;
}
.card .card-header {
  flex-direction: row;
  display: flex;
  align-items: center;
}
.card .card-header .title {
  font-size: 18px;
  line-height: 1.5;
}
.card .card-header .icon {
  margin-right: 10px;
}
.card .card-header .icon.dashicon {
  width: 33px;
  height: 33px;
  fill: #0073AA;
}
.card .card-content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.card .card-content a, .card .card-content button {
  align-self: flex-start;
  margin-top: auto;
}
.card p {
  font-size: 15px;
  color: #616161;
}
.card .card-description {
  margin: 15px 0 25px;
}

.card.starter-site-card {
  padding: 0;
  max-width: 100%;
}
.card.starter-site-card:hover .actions, .card.starter-site-card:hover .fav, .card.starter-site-card:focus-within .actions, .card.starter-site-card:focus-within .fav {
  opacity: 1;
}
.card.starter-site-card .actions.visible {
  opacity: 1;
}
.card.starter-site-card .top {
  position: relative;
  overflow: hidden;
}
.card.starter-site-card .fav {
  position: absolute;
  z-index: 2000;
  left: -15px;
  top: -30px;
  opacity: 0;
  transition: 0.3s ease;
}
.card.starter-site-card .fav button {
  padding: 30px 5px 30px 30px;
  height: auto;
  transform: rotate(45deg);
  background-color: #4268CF;
}
.card.starter-site-card .fav svg {
  transform: rotate(-45deg);
  fill: #fff;
  max-width: 15px;
}
.card.starter-site-card .actions {
  background-color: rgba(0, 0, 0, 0.75);
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  opacity: 0;
  transition: 0.3s ease;
}
.card.starter-site-card .actions .templates {
  color: #fff;
}
.card.starter-site-card .actions .components-button {
  margin: 5px 0;
  width: 100px;
  height: 40px;
  font-weight: 600;
  justify-content: center;
}
.card.starter-site-card .actions .components-button.is-secondary {
  color: #fff;
  border: 2px solid #fff;
}
.card.starter-site-card .image {
  height: 420px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  background-color: rgba(100, 109, 130, 0.25);
}
.card.starter-site-card img {
  width: 100%;
  display: block;
}
.card.starter-site-card .bottom {
  display: flex;
  align-items: center;
  padding: 15px 10px 15px 30px;
  border-top: 1px solid #e1e1e1;
}
.card.starter-site-card .bottom p {
  font-size: 16px;
  font-weight: 700;
  color: #282828;
  margin: 0;
}
.card.starter-site-card.has-templates:after {
  position: absolute;
  width: 93%;
  background-color: #FFFFFF;
  display: block;
  height: 10px;
  content: "";
  margin: 0 auto;
  bottom: -7px;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  border: 1px solid #D9D9D9;
}

.ob-preview {
  background-color: #fff;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100000;
  height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
}
.ob-preview .loading {
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100000;
  color: #646D82;
}
.ob-preview .loading svg {
  animation-name: spin;
  animation-duration: 2000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.ob-preview .preview {
  display: flex;
  flex-direction: column;
  position: relative;
  flex-grow: 1;
}
.ob-preview .preview iframe {
  flex-grow: 1;
  position: relative;
  z-index: 100001;
  height: 100%;
  width: 100%;
}
.ob-preview .bottom-bar {
  border-top: 1px solid #e1e1e1;
  display: flex;
  padding: 15px 30px;
  align-items: center;
}
.ob-preview .bottom-bar .actions {
  margin-left: auto;
}
.ob-preview .bottom-bar .heading {
  margin: 0 0 0 20px;
}

.ob-import-modal .stepper li {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  font-size: 15px;
}
.ob-import-modal .stepper li .icon {
  margin-right: 20px;
  background-color: #0073AA;
  color: #fff;
  padding: 5px;
  border-radius: 100%;
}
.ob-import-modal .stepper li .icon.success {
  background-color: #6EB77A;
}
.ob-import-modal .stepper li .icon.success svg {
  right: 1px;
  position: relative;
}
.ob-import-modal .stepper li .icon.warning {
  background-color: #ffb900;
}
.ob-import-modal .stepper li .icon.error {
  background-color: #EC7C7C;
}
.ob-import-modal .stepper li .icon.skip {
  background-color: #e1e1e1;
}
.ob-import-modal .stepper li .icon.loading .dashicon, .ob-import-modal .stepper li .icon.loading svg {
  animation-name: spin;
  animation-duration: 2000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.ob-import-modal .stepper li svg {
  width: 20px;
  height: 20px;
  display: block;
}

