.migration .modal-body .loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 30px auto;
  width: auto;
  color: #fff;
}
.migration .modal-body .loading h3 {
  margin: 20px 0 0;
}
.migration .modal-body .loading svg {
  padding: 10px;
  background-color: #ffb900;
  border-radius: 100%;
  animation-name: spin;
  animation-duration: 2000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.ob-import-modal {
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.ob-import-modal .components-modal__content {
  padding: 0 30px 40px;
}
.ob-import-modal .components-modal__header {
  border: none;
  margin-bottom: 10px !important;
}
.ob-import-modal .header {
  margin-bottom: 15px;
}
.ob-import-modal .header h1 {
